.main--404--container {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 10px 0;
  width: 760px;
  max-width: 95%;
  margin: 2em auto;
  text-align: center;
}
.main--404--container h1 {
  font-size: 2rem;
  font-weight: 500;
  text-transform: uppercase;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.25);
  margin-top: 1rem;
  color: var(--h1-color);
  font-family: var(--secondary-font);
}
.main--404--container p {
  color: #555;
}

img {
  display: block;
  width: 90%;
  margin: 0 auto;
}

/* *************************
MEDIA QUERIES
************************** */
@media screen and (max-width: 480px) {
  img {
    width: 95%;
  }
}